body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-clock {
    background: white;
    padding: 5px;
    color: #007ac9;
    border-radius: 4px;
}
.scheduled {
    width: 90%;
    margin-left: 5%;
    line-height: 50px;
}

table {
    border-collapse: collapse;
    border-style: hidden;
}

table td, table th {
    border-bottom: 1px solid white;
}

table .bold {
    margin: 5px;
    font-weight: bold;
}

.App {
  background: #007ac9;
  height: 100vh;
  color: white;
  font-size: calc(10px + 2vmin);
}

.App-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  color: white;
  padding: 20px 5%;
}
